* {
  padding: 0; 
  margin: 0;
}

body {
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 13px;
}

header {
  position: fixed;
  width: 100%;
  display: flex;
  align-items: center;
  height: 44px;
  padding-left: 20%;
  background-color: rgb(219, 76, 63);
  color: #fff;
  user-select: none;
  z-index: 9;
}

#search-form {
  margin-left: 15%;
  display: flex;
  color: #29662e;
}

#search {
  width: 250px;
  display: inline-block;
  padding: 8px;
  border: none;
  border-radius: 10px;
  outline: none;
  font-size: 1em;
  color: black;
  background-color: rgb(219, 76, 63);
}

#search:hover {
  background-color: rgb(226, 112, 101);
}

#search:focus {
  background-color: white;
  border: 1px solid #ac4e4e;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

input[type="search"]::-webkit-input-placeholder {
  color: white;
}

input[type="search"]:focus::-webkit-input-placeholder {
  color: gray;
}

#button-search:hover {
  background-color: #ac4e4e;
}

#button-search:active {
  background-color: #672626;
}

li {
  list-style-type: none;
}

.wrapper {
  padding-top: 44px;
  display: flex;
  justify-content: center;
}
/* Menu */

.menu {
  margin-left: 20%;
  flex-basis: 15%;
  padding-top: 20px;
  flex: 1;
  text-align: left;
  display: block;
}

.menu-item-head>h4 {
  display: inline-block;
  font-size: 1.2em;
  margin: 10px;
}

.menu-item-head {
  background-color: #fafafa;
  border-bottom: 1px solid #e2e1e1;
  user-select: none;
}

.menu-item-head:hover {
  background-color: #f7f7f7;
  cursor: pointer;
}

.menu-list {
  overflow: auto;
  max-height: 300px;
  margin-bottom: 5px;
  border-bottom: 1px solid #e2e1e1;
  overflow-x: hidden;
}

.projects-list {
  color: black;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track-piece {
  background-color: white;
}

::-webkit-scrollbar-thumb:vertical {
  height: 5px;
  background-color: silver;
}

::-webkit-scrollbar-thumb:horizontal {
  width: 5px;
  background-color: silver;
}

.menu-list>li {
  position: relative;
  font-size: 1.1em;
  padding: 5px;
  margin-bottom: 1px;
  margin-left: 15px;
}

.menu-list>li:hover {
  background-color: white;
  cursor: pointer;
}

.menu-button-add {
  margin-right: 10px;
  float: right;
  padding: 3px;
  font-size: 1.8em;
  border: none;
  color: silver;
}

.menu-button-add:hover {
  color: grey;
}

.menu-button-add:active {
  color: black;
}

.menu-item-headbutton {
  padding: 5px;
  font-size: 1em;
}

.menu-item-headbutton:hover {
  background-color: #f7f7f7;
  cursor: pointer;
  user-select: none;
}

.menu-span {
  display: inline-block;
  width: 180px;
  font-size: 1em;
  margin: 3px;
}
/* form add */

#form-send-label,
#form-send-project {
  padding: 10px;
  /* display: none; */
  background-color: white;
  padding-bottom: 5px;
  border-bottom: 1px solid #e2e1e1;
}

.form-add-div {
  width: auto;
  border-radius: 5px;
  border: 1px solid #fafafa;
}

#form-add-divlabel {
  /* display: none; */
}

.form-add-input {
  margin-top: 10px;
  width: 95%;
  padding: 5px;
  padding-left: 5px;
  border: 1px solid silver;
  border-radius: 4px;
  outline: none;
  font-size: 1em;
  color: black;
}

.content {
  margin-right: 20%;
  flex: 2.4;
  border: 1px solid rgb(240, 237, 237);
  background-color: #fff;
  padding-bottom: 250px;
  min-height: 59vh;
  z-index: 6;
}

.work-space {
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 30px;
  display: none;
}

.work-space h3 {
  padding: 30px 0 10px 0;
  user-select: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 500;
}

#new-task {
  width: 75%;
  padding: 7px;
  padding-left: 12px;
  border: 1px solid silver;
  border-radius: 4px;
  outline: none;
  font-size: 1.2em;
  color: black;
}
/* Buttons */

.red-button-accept {
  margin-top: 7px;
  padding: 8px;
  background-color: rgb(219, 76, 63);
  font-size: 1em;
  border: none;
  border-radius: 4px;
  color: #fafafa;
  outline: none;
  user-select: none;
  transition: color .2s linear;
}

.red-button-accept:hover {
  background-color: #ac4e4e;
  cursor: pointer;
}

.button-cancel {
  background-color: white;
  margin-top: 7px;
  margin-left: 3px;
  padding: 5px;
  font-size: 0.9em;
  border: none;
  color: rgb(83, 75, 75);
  outline: none;
  cursor: pointer;
  user-select: none;
  transition: color .2s linear;
}

.button-cancel:hover {
  text-decoration: underline;
}

.three-points-button {
  display: none;
  float: right;
  font-size: 1.5em;
  color: gray;
  user-select: none;
  transition: color .2s linear;
}

.menu-list>li:hover .three-points-button {
  display: block;
}

.three-points-button:hover {
  color: black;
  cursor: pointer;
}

.buttons-block {
  text-align: left;
  display: none;
  position: absolute;
  width: max-content;
  top: 0;
  right: 0;
  text-align: center;
  background-color: white;
  border: 1px solid grey;
  border-radius: 4px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 8;
}

.item-change-button,
.item-delete-button {
  margin: 5px;
  font-size: 12px;
  color: rgb(75, 75, 75);
  cursor: pointer;
  user-select: none;
  padding: 2px;
  text-decoration: none;
  display: block;
}

.item-change-button:hover,
.item-delete-button:hover {
  color: rgb(136, 128, 128);
}

.check {
  background-color: #fff;
  border: none;
  color: red;
  cursor: pointer;
}

#ul-unfinished-tasks>li,
#ul-finished-tasks>li {
  position: relative;
  width: 90%;
  border-bottom: 1px solid rgb(240, 232, 232);
  padding-right: 20px;
}

.label-tasks {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1em;
}

.ul-finished-tasks {
  margin-bottom: 300px;
}

.finished-tasks {
  grid-column: 1;
  grid-row: 4;
}

.finished-tasks .task {
  text-decoration: line-through;
}

#ul-finished-tasks>li:hover .three-points-button,
#ul-unfinished-tasks>li:hover .three-points-button {
  display: block;
}

.tasks-class {
  vertical-align: middle;
}

span.task {
  display: inline-block;
  width: 80%;
  padding: 10px;
  font-size: 1.1em;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

.selected {
  background-color: white;
}

.show {
  display: block;
}

.three-points-button:active {
  display: block;
}